<script>
    import { Player, Video, DefaultUi } from '@vime/svelte';
    import { fade, fly, slide, scale, blur } from "svelte/transition"
    import Loading from './Loading.svelte'

    export let videos
    export let poster
    export let resolution

    $: src = []
    let translations
    let loading = true
    let selected = 0

    let module = () => {
            fetch('https://38.svetacdn.in/api/translations?api_token=caPViSv7B4vYtQeyX0bAAdcHxSOQSc1O')
                .then(response => response.json())
                .then((function(result) {
                    translations = result
                    loadVideos()
                    loading = false
                })
            )}
     module()
    async function loadVideos() {
         try { 
            let res = await fetch('https:'+videos);
            let filmsPage = await res.text();
            if(res.ok) {
                let parser = new DOMParser();
                let doc = parser.parseFromString(filmsPage, 'text/html');
                let files = doc.getElementById("files").value
                let jsonFilm = files.replaceAll('\\', '')
                let filmObject = JSON.parse(jsonFilm);
                
                function translation(id) {
                    let result = translations.data
                    return result.filter(x => x.id == id).map(x => x.short_title)[0]
                }

                const checkurl = () => {
                    Object.entries(filmObject).forEach(element => {
                        let id = element[0]

                        let strValue = element[1]
                        strValue = strValue.replaceAll(' or //', ',')
                        strValue = strValue.split(',')
                        let str = strValue[13].slice(0, -7)
                        src = [...src, {'id': id, 'src': str, 'translation': translation(id)}]
                        src=src

                    });
                }
                checkurl()
                console.log(src)

            }
        } catch (error) {
            console.error('Ошибка:', error);
        }
    }


</script>

{#if loading === true}
    <Loading />
{:else if translations !== undefined}
<div class="card p-3" transition:fade>
    <p>Выбрать перевод</p>
    <select class='m-2' bind:value={selected}>
        {#each src as {translation, id}, i}
            <option value={i}>
                <p>{translation}</p>
            </option>
        {/each}
    </select>

    {#each src as {src}, i}
        {#if i==selected}
                <Player
                    theme="dark"
                    style="--vm-player-theme: #e86c8b;">
                <Video
                    poster="{poster}">
                    <source 
                        data-src="https://{src}{resolution}.mp4" 
                        type="video/mp4"/>
                </Video>

                <DefaultUi />
                </Player>
        {/if}
    {/each}
</div>
{/if}
